<template>
  <div class="wyl_big">
    <!-- header  面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>分类参数</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- header end -->

    <!-- 卡牌视图   start -->
    <el-card>
      <!-- warning -->
      <el-alert show-icon :closable="false" title="注意：只允许为第三级分类设置相关参数！" type="warning"></el-alert>
      <!--选择商品分类区域  -->
      <el-row>
        <el-col>
          <span>选择商品分类:</span>
          <!-- 级联选择框 -->
          <el-cascader
            expand-trigger="hover"
            v-model="selectedParamsKeys"
            :options="ParamsList"
            :props="ParamsProps"
            @change="handleChange"
          ></el-cascader>
        </el-col>
      </el-row>
      <!-- tabs  -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!--  动态参数 -->
        <el-tab-pane label="动态参数" name="many">
          <el-button
            @click="addDialogVisible=true"
            type="primary"
            size="mini"
            :disabled="isBtnDisabled"
          >添加参数</el-button>
          <el-table :data="manyTableData" border stripe>
            <!-- 展开行 -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- tag  for -->
                <el-tag
                  v-for="(m,i ) in scope.row.attr_vals"
                  :key="i"
                  closable
                  @close="handleClose(i,scope.row)"
                >{{m}}</el-tag>
                <!-- text -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <!-- button -->
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                >+ New Tag</el-button>
              </template>
            </el-table-column>
            <!-- 索引咧 -->
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column lebel="参数名称" prop="attr_name"></el-table-column>
            <el-table-column lebel="操作">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-edit"
                  @click="showEditDialog(scope.row.attr_id)"
                >编辑</el-button>
                <el-button
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  @click="removeParams(scope.row.attr_id)"
                >删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!--  静态属性 -->
        <el-tab-pane label="静态属性" name="only">
          <el-button
            @click="addDialogVisible=true"
            type="primary"
            size="mini"
            :disabled="isBtnDisabled"
          >添加属性</el-button>
          <el-table :data="onlyTableData" border stripe>
            <!-- 展开行 -->
            <el-table-column type="expand">
                  <template slot-scope="scope">
                <!-- tag  for -->
                <el-tag
                  v-for="(m,i ) in scope.row.attr_vals"
                  :key="i"
                  closable
                  @close="handleClose(i,scope.row)"
                >{{m}}</el-tag>
                <!-- text -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <!-- button -->
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                >+ New Tag</el-button>
              </template>
            </el-table-column>
            <!-- 索引咧 -->
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column lebel="属性名称" prop="attr_name"></el-table-column>
            <el-table-column lebel="操作">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-edit"
                  @click="showEditDialog(scope.row.attr_id)"
                >编辑</el-button>
                <el-button
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  @click="removeParams(scope.row.attr_id)"
                >删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!-- 卡牌视图   end -->

    <!-- 添加参数的对话框  添加属性的对话框   start-->
    <el-dialog
      :title="titleText"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <el-form ref="addFormRef" :model="addForm" :rules="addFormRules" label-width="80px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addParams">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 添加参数的对话框  添加属性的对话框   end-->

    <!-- 修改参数的对话框  添加属性的对话框   start-->
    <el-dialog title="修改" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
      <el-form ref="editFormRef" :model="editForm" :rules="editFormRules" label-width="80px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editParams">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改参数的对话框  添加属性的对话框   end-->
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      // 商品分类列表
      ParamsList: [],
      // 级联选择框的配置对象
      ParamsProps: {
        value: "cat_id",
        label: "cat_name",
        children: "children"
      },
      //   级联选择框De双向数据绑定
      selectedParamsKeys: [],
      //  被激活的页签名字
      activeName: "many",
      //   动态参数数据
      manyTableData: [],
      //   静态属性的数据
      onlyTableData: [],
      //   添加对话框的显示隐藏
      addDialogVisible: false,
      //表单数据验证
      addForm: {
        attr_name: ""
      },
      // 表单数据验证 规则
      addFormRules: {
        attr_name: [{ required: true, message: "请输入", trigger: "blur" }]
      },

      //   编辑的 对话框的显示隐藏
      editDialogVisible: false,
      // 修改表单数据对象
      editForm: {},
      //  修改表单的验证规则对象
      editFormRules: {
        attr_name: [{ required: true, message: "请输入", trigger: "blur" }]
      }
    };
  },
  props: {},
  components: {},
  mounted() {},
  created() {
    this.getParamsList();
    console.log(this.selectedParamsKeys.length);
  },
  computed: {
    //   当选择完成 级联选择器时，按钮的禁用状况
    isBtnDisabled() {
      if (this.selectedParamsKeys.length !== 3) {
        return true;
      }
      return false;
    },
    // 当前选中的级联分类deId
    ParamsId() {
      if (this.selectedParamsKeys.length == 3) {
        return this.selectedParamsKeys[2];
      }
      return null;
    },
    // 添加参数的对话框  添加属性的对话框   文本的转换
    titleText() {
      if (this.activeName == "only") {
        return "静态属性";
      }
      return "动态参数";
    }
  },
  methods: {
    async getParamsList() {
      let { data: res } = await this.$http.get(`categories`);
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error("分类参数接口获取失败");
      }
      this.ParamsList = res.data;
      console.log(this.ParamsList);
    },
    // 级联选择器中  选择的数据
    async handleChange() {
      this.getParamsData();
    },
    // tabs 点击事件的处理函数
    handleClick() {
      this.getParamsData();
      console.log(this.activeName);
    },
    //
    async getParamsData() {
      console.log(this.selectedParamsKeys);
      let a = this.selectedParamsKeys.length;
      if (a !== 3) {
        this.selectedParamsKeys = [];
        return a;
      }
      console.log(a);
      //   格局所选的分类id，和当前所处的面板，获取对应的参数

      let { data: res } = await this.$http.get(
        `categories/${this.ParamsId}/attributes`,
        {
          params: { sel: this.activeName }
        }
      );
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      }
      //
      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(",") : [];
        // 控制文本框的显示隐藏
        item.inputVisible = false;
        // 文本框中输入的值
        item.inputValue = "";
      });

      if (this.activeName === "many") {
        this.manyTableData = res.data;
      } else {
        this.onlyTableData = res.data;
      }
    },
    // 当添加参数数据关闭时 重置
    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
    },
    //
    addParams() {
      this.$refs.addFormRef.validate(async vaild => {
        if (!vaild) {
          return this.$message.error(res.meta.msg);
        }
        let { data: res } = await this.$http.post(
          `categories/${this.ParamsId}/attributes`,
          {
            attr_name: this.addForm.attr_name,
            attr_sel: this.activeName
          }
        );
        console.log(res);
        if (res.meta.status !== 201) {
          return this.$message.error("分类参数接口获取失败");
        }
        this.addDialogVisible = false;
        this.getParamsData();
      });
    },
    // 点击按钮，展示修改按钮的对话框
    async showEditDialog(attr_id) {
      let { data: res } = await this.$http.get(
        `categories/${this.ParamsId}/attributes/${attr_id}`,
        {
          paremas: {
            attr_sel: this.activeName
          }
        }
      );
      if (res.meta.status !== 200) {
        return this.$message.error("参数获取失败");
      }
      this.editForm = res.data;
      this.editDialogVisible = true;
    },
    // / 当 修改 参数数据关闭时 重置
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
    },
    // 点击确认按钮，展示修改参数信息
    async editParams() {
      this.$refs.editFormRef.validate(async vaild => {
        if (!vaild) {
          return this.$message.error(res.meta.msg);
        }
        let { data: res } = await this.$http.put(
          `categories/${this.ParamsId}/attributes/${this.editForm.attr_id}`,
          {
            attr_name: this.editForm.attr_name,
            attr_sel: this.activeName
          }
        );
        console.log(res);
        if (res.meta.status !== 200) {
          return this.$message.error("参数获取失败jijijijijijjj");
        }
        this.$message.success("修改数据成功");
        this.getParamsData();
        this.editDialogVisible = false;
      });
    },
    //依局id  删除对应弹框
    async removeParams(id) {
      let con = await this.$confirm(
        "此操作将永久删除该用户, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }
      ).catch(err => err);

      if (con !== "confirm") {
        return this.$message.info("已取消删除");
      }

      let { data: res } = await this.$http.delete(
        `categories/${this.ParamsId}/attributes/${id}`
      );
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      } else {
        this.$message.success("用户删除成功");
        this.getParamsData();
      }
    },
    // 文本框 失去焦点，或按下 enter 都会触发
    async handleInputConfirm(row) {
      console.log("ok");

      if (row.inputValue.trim().length === 0) {
        row.inputValue = "";
        row.inputVisible = false;
        return;
      }

      //   如果没有return 就证明有内容
      row.attr_vals.push(row.inputValue.trim());
      row.inputValue = "";
      row.inputVisible = false;

      //   发起网络请求  保存操作
      let { data: res } = await this.$http.put(
        `categories/${this.ParamsId}/attributes/${row.attr_id}`,
        {
          attr_name: row.attr_name,
          attr_vals: row.attr_vals.join(" "),
          attr_sel: row.attr_sel
        }
      );
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      }

      this.$message.success("修改参数项成功");
    },
    // 点击按钮，输入文本框
    showInput(row) {
      row.inputVisible = true;
      //   让文本自动获得焦点
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    //
    async handleClose(i, row) {
      row.attr_vals.splice(i, 1);

      //   发起网络请求  保存操作
      let { data: res } = await this.$http.put(
        `categories/${this.ParamsId}/attributes/${row.attr_id}`,
        {
          attr_name: row.attr_name,
          attr_vals: row.attr_vals.join(" "),
          attr_sel: row.attr_sel
        }
      );

      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      }

      this.$message.success("修改参数项成功");
    }
  }
};
</script>

<style scoped lang='scss'>
.el-row {
  margin: 0.15rem 0;
}
.el-tag {
  margin: 0.05rem;
}
.input-new-tag {
  width: 1.2rem;
}
</style>